.iconBox_item{
    padding: 30px;
    border:3px solid var(--border-color);
    background:rgb(37 36 36 / 8%);
    position: relative;
    height: 100%;
    border-radius: 10px;
    .box_icon{
        width: 70px;
        height: 70px;
        img{
            width: 100%;
            height: 100%;
            object-fit: contain;
        }
    }
    h5{
        font-size: 30px;
        font-weight: 500;
        line-height: 1.3;
        margin-top: 30px;
    }
    .text{
        margin-top: 20px;
        p{
            margin-bottom: 0;
        }
    }
}