@tailwind base;
@tailwind components;
@tailwind utilities;
@import "./fonts.css";

:root {
  --primary: #b036d9; 
  --secondary: #1bffce;
  --color-third: #d936c7;
  --color-forth: #6838d9;
  --border-color:#55156c;
  --dark: #6f237e; 
  --bg:#10073C;
}

* {
  margin: 0;
  padding: 0;
  -webkit-box-sizing: border-box;
  box-sizing: border-box;
  outline: none;
  list-style: none;
}
body {
  background-color: #10073c;
  color: white;
  margin: 0;
  font-family: "Satoshi", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  line-height: 1.5;
  font-size: 17px;
  font-weight: normal;
  overflow-x: hidden !important;

  p {
    font-weight: 400;
    color: #c6c8c9;
    margin: 0;
    margin-bottom: 15px;
}
  .page-section {
    padding: 80px 0;
  }
}

h1, h2, h3, h4, h5, h6 {
  font-weight: 500;
  line-height: 1.3;
  margin-bottom: .2rem;
}

h1 {
  font-size: 70px;
}

h2 {
  font-size: 50px;
}

h3 {
  font-size: 40px;
}

h4 {
  font-size: 30px;
}

h5 {
  font-size: 25px;
}

h6 {
  font-size: 20px;
}

a, a:hover {
  color: inherit;
}

span {
  display: inline-block;
}

a {
  display: inline-block;
  text-decoration: none;
}

a:hover {
  text-decoration: none;
}

img {
  width: 100%;
  height: auto;
}

figure {
  margin: 0;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.slick-slider{
  .slick-track{
    display: flex;
    .slick-slide{
      height: auto;
      float: none;
      & > div{
        height: 100%;
      }
    }
  }
}

.primary_overlay{
  position: absolute;
  width: 100%;
  height: 100%;
  background-color: var(--dark);
  opacity: 0.85;
  left: 0;
  top: 0;
  z-index: 0;
}

[data-cursor-size]::before, [data-cursor-background-image]::before, [data-cursor-color]::before, [data-cursor-exclusion]::before, [data-cursor-text]::before, [data-cursor-stick]::before, [data-cursor-magnetic]::before{
  pointer-events: none;
}
.shape-1::after{
  width: 500px;
  border-radius: 0 100% 100% 0;
  content: "";
  background: var(--dark);
  position: absolute;
  left: -250px;
  top: -250px;
  height: 500px;
  filter: blur(120px);
  z-index: -1;
  opacity: 0.8;
}
.shape-2::before{
  width: 500px;
  border-radius: 0 100% 100% 0;
  content: "";
  background: var(--dark);
  position: absolute;
  right: 0px;
  top: 40%;
  height: 500px;
  filter: blur(120px);
  z-index: -1;
  opacity: 0.6;
}
.pt-0{
  padding-top: 0 !important;
}
.App-content{
  min-height: 400px;
}








.caseStudy_grid {
  display: flex;
  flex-wrap: wrap;
}

.caseStudy_grid .caseStudy_item {
  width: calc(100% / 3);
}

.sc_inner {
  position: relative;
  padding: 0px;
}

.sc_inner .cs_thumbnail {
  height: 330px;
  width: 100%;
  overflow: hidden;
  border-radius: 20px;
}

.sc_inner .cs_thumbnail img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.cs_content {
  text-align: center;
  width: calc(100% - 60px);
  margin: 0 auto;
  padding: 20px;
  background: var(--border-color);
  border: 3px solid var(--border-color);
  border-radius: 10px;
  position: relative;
  margin-top: -30px;
}

.cs_content h3 {
  font-size: 22px;
}