.value_section {
  // border-top: 1px solid #403b3b;
  // background: #1d1d1d;
}
.slider_item {
  padding: 15px;
  height: 100%;
}

.about_teamSection {
  background: var(--dark);
}

.team_grid {
  display: flex;
  flex-wrap: wrap;
  .team_item {
    margin-bottom: 20px;
    max-width: 930px;
    width: 100%;
    &:nth-child(2) {
      margin-right: 0;
      margin-left: auto;
    }
  }
}
.team_box_inner {
  padding: 30px;
  /* border: 1px solid #ffffff82; */
  border-radius: 20px;
  background: #00000026;
  backdrop-filter: blur(10px);
  display: flex;
  flex-wrap: wrap;

  .team_img {
    width: 30%;
    padding-right: 2%;
    img{
        border-radius: 20px;
    }
  }

  .team_details {
    width: 70%;
    .team_name {
      font-size: 30px;
      font-weight: 600;
    }
    .team_role {
      margin-top: 4px;
      margin-bottom: 11px;
      text-transform: uppercase;
      font-size: 13px;
      font-weight: 700;
      color: var(--color-third);
    }
    .team_content p {
      color: #fffffff5;
    }
  }
}




.whyUsList_item {
    padding: 20px 30px;
    display: flex;
    margin-bottom: 9px;
    border-radius: 10px;
    border: 2px solid var(--border-color);
    align-items: center;
    transition: 0.4s all linear;
    .wcu_icon {
      font-size: 48px;
      margin-right: 16px;
      color: var(--secondary);
    }
    &.active{
      background: var(--primary);
      .wcu_icon{
        color: #fff;
      }
    }
}


.whyUsList_Content_wrp {
    // background: #1d1d1d;
    padding: 0;
    border-radius: 20px;
    height: 100%;
}
.whyUsList_Content img{
    border-radius: 20px;
    margin-bottom: 10px;
}
.whyUsList_Content h3 {
    font-size: 30px;
    font-weight: 600;
    color: var(--primary);
    margin-bottom: 13px;
    padding-left: 40px;
    padding-right: 40px;
}

.whyUsList_Content p {
    padding-left: 40px;
    padding-right: 40px;
    // font-size: 20px;
    // line-height: 1.6;
}