.page-header{
    padding: 100px 0;
    position: relative;
    > .max-w-container{
        position:relative;
        z-index: 2;
    }
    .page_header_overlay{
        opacity: 0.8;
    background: #10073c;
    top: 0;
    z-index: 0;
    content: "";
    position: absolute;
    bottom: -2px;
    left: 0;
    width: 100%;
    height: 100%;
    background-image: linear-gradient(180deg, #1a1a1a, transparent);
    }
}
