.sidebar {
  .widget {
    margin-bottom: 50px;

    .title-widget {
      margin-bottom: 20px;
      padding-top: 20px;
      border-top: 1px solid rgba(255, 255, 255, 0.08);
      text-transform: uppercase;
      letter-spacing: 1px;
      position: relative;
      font-size: 20px;

      &::after {
        content: '';
        position: absolute;
        right: 0;
        top: 0;
        height: 30px;
        border-right: 1px solid rgba(255, 255, 255, 0.08);
      }
    }

    ul {
      li {
        display: flex;
        &.category_item{
          a{
            font-size: 15px;
            margin-bottom: 7px;
            padding: 10px 20px;
            background: rgb(255 255 255 / 5%);
            border-radius: 30px;
            width: 100%;
            &:hover{
              background: var(--primary);
            }
          }
        }
        &.feature_post_item {
          padding: 0;
          background: transparent;
          margin-bottom: 24px;

          .feature_post_img {
            width: 80px;
            height: 80px;
            border-radius: 10px;
            overflow: hidden;

            img {
              width: 100%;
              height: 100%;
              object-fit: cover;
            }
          }

          .feature_post_content {
            width: calc(100% - 80px);
            padding-left: 15px;

            .post-category {
              display: inline-block;
              padding: 5px 20px;
              border-radius: 20px;
              line-height: normal;
              background: var(--dark);
              font-size: 11px;
              text-transform: uppercase;
              font-weight: 500;
            }

            h4 {
              font-size: 16px;
              margin-top: 5px;
            }
          }
        }
      }
    }
  }
}
