.sectionHeading_wrp {
  margin-bottom: 20px;
  .sec_title {
    font-size: 50px;
    font-weight: 600;
  }
  .sec_sub_title {
    padding: 8px 20px;
    border: 2px solid var(--border-color);
    border-radius: 30px;
    display: inline-block;
    font-size: 13px;
    font-weight: 600;
    text-transform: uppercase;
    color: var(--primary);
    background: var(--bg);
  }
  p {
    margin: 0;
  }
}
