.serviceDetail_page {
  .service_top_content {
    font-size: 21px;
    margin-bottom: 50px;
    p {
      color: #fff;
    }
  }
  .serviceImg {
    height: 360px;
    border-radius: 20px;
    overflow: hidden;
    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }
  .service_list_content {
    display: flex;
    flex-wrap: wrap;
    .serv_list_item {
      padding: 10px;
      width: calc(100% / 2);
      .service_list_inner {
        padding: 20px;
        border: 2px solid var(--border-color);
        border-radius: 12px;
        background: #10073c33;
        height: 100%;
        .serviceList_content h4 {
          font-size: 23px;
          margin-bottom: 12px;
        }
      }
    }
  }

  .service_bottom_content p {
    font-size: 21px;
    color: var(--secondary);
  }
}
