.postGrid_box {
    display: flex;
    flex-wrap: wrap;
    position: relative;
    margin-left: -15px;
    margin-right: -15px;
}.postGrid_box .post_itemBox {
    width: 50%;
    position: relative;
    margin-bottom: 50px;
    padding: 0 15px;
}

.postGrid_box .post_itemBox .post_innerBox {
    padding: 0 0px;
    position: relative;
    height: 100%;
}

.postGrid_box .post_itemBox .post_innerBox .post_img {
    height: 290px;
    position: relative;
    overflow: hidden;
    border-radius: 20px;
    width: 100%;
}

.postGrid_box .post_itemBox .post_innerBox .post_img img {
    width: 100%;
    height: 100%;
    object-fit: cover;
}

a.post_img {}

.postGrid_box .post_itemBox .post_innerBox .post_content {
    background: var(--bg);
    padding: 20px 27px;
    width: calc(100% - 40px);
    margin: 0 auto;
    position: relative;
    margin-top: -80px;
    border: 3px solid var(--border-color);
    border-radius: 16px;
    height: calc(100% - 214px);
}

.postGrid_box .post_itemBox .post_innerBox .post_content .post-title {
    margin-bottom: 10px;
    font-size: 20px;
}

.postGrid_box .post_itemBox .post_innerBox .post_content .post-info a {
    display: flex;
    flex-wrap: nowrap;
    align-items: center;
}


.postGrid_box .post_itemBox .post_innerBox .post_content .post-info a svg {
    margin-left: 9px;
}

.postGrid_box .post_itemBox .post_innerBox .post_content .post-category {
    background: var(--dark);
    display: inline-block;
    padding: 6px 20px;
    line-height: normal;
    border-radius: 20px;
    font-size: 12px;
    text-transform: uppercase;
    font-weight: 500;
    margin-bottom: 14px;
}

.postGrid_box .post_itemBox .post_innerBox .post_content .post-date {
    margin: 0;
    font-size: 14px;
}




.topInfo_wrp {
    display: flex;
}

.left_tb {
    display: flex;
    align-items: center;
}

.blog_single_page-header h1 {
    font-size: 50px;
    line-height: 1.3;
    margin: 20px 0 40px;
}

.topInfo_author {
    margin-right: 70px;
}

.authorImg {
    width: 70px;
    height: 70px;
    background: var(--border-color);
    font-size: 0;
}

.topInfo_wrp label {
    font-size: 14px;
    opacity: 0.6;
}

.topInfo_wrp h6 {
    font-size: 16px;
}

.post-content > p:first-child:first-letter {
    font-size: 60px;
    line-height: 50px;
    padding-right: 5px;
    float: left;
}

.post-content {
    position: relative;
    color: #fff;
}

.post-content p {
    color: #fff;
    opacity: 0.8;
}

.post-content h2 {
    font-size: 40px;
    margin: 40px 0 20px;
    color: var(--primary);
}

.post-content h3 {
    font-size: 28px;
    margin: 30px 0 6px;
}